<template>
  <div class="plan-add-content oct-flex-col-all-center">
    <div class="oct-flex-col-all-center plan-time-picker">
      <div class="plan-picker-title">选择时间</div>
      <div class="oct-flex-row-all-center" style="width: 100%">
        <van-datetime-picker v-model="startTime"
                             type="time"
                             :show-toolbar="false"
                             :visible-item-count="4"
                             @change="handleChange">
        </van-datetime-picker>
        <div style="margin: 10px"><span style="font-weight: bold;font-size: 20px;">-</span></div>
        <van-datetime-picker v-model="endTime"
                             type="time"
                             :show-toolbar="false"
                             :visible-item-count="4"
                             @change="handleChange">
        </van-datetime-picker>
      </div>
      <div style="width: 100%;margin-top: 5px;" class="plan-input-box">
        <van-cell-group>
          <van-cell title="学习时间" :label="timeLabel" :value="getTimeValue()"></van-cell>
          <van-cell title="学习科目" :value="subject" is-link arrow-direction="down" @click="selectSubject = true"></van-cell>
          <van-field
            v-model="content"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="200"
            placeholder="请输入留言"
            show-word-limit
          ></van-field>
          <el-button class="plan-add-btn" @click="addPlan" type="text">{{changeMode ? '修改' : '添加'}}</el-button>
        </van-cell-group>
      </div>
    </div>
    <div style="width: 100%;margin-top: 10px;border-radius: 10px;overflow-y: auto;max-height: 100vh;min-height: 100vh">
      <oct-all-plan :plans="plans" @reload="initData"></oct-all-plan>
    </div>
    <van-action-sheet v-model="selectSubject" :actions="subjects" @select="onSelect"></van-action-sheet>
  </div>
</template>

<script>
import OctAllPlan from '@/components/plan/OctAllPlan'
import { getPlanByPage, savePlan, changePlan } from '@/api/study'

export default {
  name: 'OctAddPlan',
  components: { OctAllPlan },
  data () {
    return {
      changeMode: false,
      plan: {},
      date: new Date(),
      dateValidate: true,
      timeLabel: '结束时间请大于起始时间',
      plans: [],
      total: 0,
      loading: false,
      finished: false,
      content: '',
      selectSubject: false,
      subject: '高数',
      subjects: [
        { name: '高数' },
        { name: '英语' },
        { name: '专业课' }
      ],
      startTime: new Date().getHours() + ':' + new Date().getMinutes(),
      endTime: new Date().getHours() + ':' + new Date().getMinutes(),
      showValue: '',
      page: {
        startPage: -1,
        pageNum: 5
      }
    }
  },
  mounted () {
    this.initData()
    if (this.$route.params.mode === 'add') {
      if (this.$route.params.date !== undefined) {
        this.date = this.$route.params.date
      } else {
        this.date = new Date()
      }
    } else {
      this.plan = this.$route.params.plan
      this.changeMode = true
      this.date = new Date(this.plan.startTime)
      this.startTime = new Date(this.plan.startTime).getHours() + ':' + new Date(this.plan.startTime).getMinutes()
      this.endTime = new Date(this.plan.endTime).getHours() + ':' + new Date(this.plan.endTime).getMinutes()
      this.showValue = this.$moment(this.date).format('MM月DD日') + ' ' + this.startTime + '-' + this.endTime
      this.content = this.plan.content
      this.subject = this.plan.subject
    }
  },

  watch: {
    startTime (newVal) {
      if (newVal > this.endTime) {
        this.$dom.getElByClassName(this, 'van-cell__label')[0].style = 'color: rgba(139, 0, 0, 0.8) !important;'
        this.dateValidate = false
      } else {
        this.$dom.getElByClassName(this, 'van-cell__label')[0].style = 'color: #969799 !important;'
        this.dateValidate = true
      }
    },
    endTime (newVal) {
      if (newVal < this.startTime) {
        this.$dom.getElByClassName(this, 'van-cell__label')[0].style = 'color: rgba(139, 0, 0, 0.8) !important;'
        this.dateValidate = false
      } else {
        this.$dom.getElByClassName(this, 'van-cell__label')[0].style = 'color: #969799 !important;'
        this.dateValidate = true
      }
    }
  },
  methods: {
    getTimeValue () {
      if (this.changeMode) {
        return this.showValue
      } else {
        return this.$moment(this.date).format('MM月DD日 ') + this.startTime + ' - ' + this.endTime
      }
    },
    async initData () {
      await getPlanByPage(this.page)
        .then((res) => {
          this.plans = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item) => {
              if (item.type === 1 && this.$moment(new Date(item.startTime)).format('YYYYMMDD') === this.$moment(this.date).format('YYYYMMDD')) {
                this.plans.push(item)
              }
            })
          }
          // if (res.data.data.length > 0) {
          //   this.total = res.data.data[0].total
          //   let result = res.data.data
          //   result.forEach((item) => {
          //     if (this.plans.find((plan) => plan.planId === item.planId) === undefined) {
          //       if (item.type === 1 && this.$moment(new Date(item.startTime)).format('YYYYMMDD') === this.$moment(this.date).format('YYYYMMDD')) {
          //         this.plans.push(item)
          //       }
          //     }
          //   })
          // }
          // // 加载状态结束
          // this.loading = false
          // // 数据全部加载完成
          // if (res.data.data.length === 0 || this.plans.length === 0) {
          //   this.finished = true
          // } else {
          //   this.page.startPage += 1
          // }
        })
    },
    async addPlan () {
      if (this.dateValidate) {
        if (!this.changeMode) {
          const data = {
            startTime: this.convertDate(this.startTime),
            endTime: this.convertDate(this.endTime),
            content: this.content,
            subject: this.subject,
            type: 1,
            status: 1
          }
          await savePlan(data)
            .then(() => {
              this.plans.push(data)
            })
        } else {
          const data = {
            startTime: this.convertDate(this.startTime),
            endTime: this.convertDate(this.endTime),
            content: this.content,
            subject: this.subject,
            type: 1,
            status: this.plan.status,
            planId: this.plan.planId
          }
          await changePlan(data)
            .then(() => {
              this.$notify.success('修改成功')
              this.$router.isBack = true
              this.$router.goBack()
            })
        }
      }
    },
    convertDate (data) {
      return new Date(this.date.getFullYear(), this.date.getMonth(), this.date.getDate(), data.split(':')[0], data.split(':')[1])
    },
    // nextPage (isLoading, isFinish) {
    //   this.loading = isLoading
    //   this.finished = isFinish
    //   this.initData()
    // },
    handleEndDateConfirm (data) {
      console.log(data)
    },
    handleCancel () {
    },
    handleChange () {
    },
    onSelect (data) {
      this.subject = data.name
      this.selectSubject = false
    }
  }
}
</script>

<style lang="scss">
  .plan-input-box {
    .van-cell_red {
      color: rgba(139, 0, 0, 0.8) !important;
    }
  }
  .van-picker-column__item--selected {
    font-size: 24px !important;
  }
  .van-picker {
    background-color: $bg-color;
    width: 40% !important;
    border-radius: 10px;
  }
  .van-ellipsis {
    background-color: $bg-color;
  }
  .van-picker__columns {
    border-radius: 10px;
  }
  .van-picker-column {
    border-radius: 10px;
  }
  .van-picker__mask {
    border-radius: 10px;
  }
  .van-cell-group {
    background-color: $bg-color !important;
  }
  .van-cell {
    background-color: $bg-color !important;
  }
  .van-cell-group {
    border-radius: 10px;
    border-bottom: none;
  }
  .van-field {
    border-radius: 10px;
  }
</style>
<style scoped lang="scss">
  .plan-subject {
    background-color: $bg-color;
  }
  .plan-add-content {
    padding: 10px;
  }
  .plan-time-picker {
    width: 100%;
    border-radius: 10px;
    background-color: $bg-color;
  }
  .plan-picker-title {
    margin: 10px;
    font-size: 14px;
  }
  .plan-add-btn {
    width: 100%;
    background-color: rgba(125, 105, 78, 0.8);
    color: white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }

</style>
